import React from 'react';
import {Component} from 'react';
import {Navbar, Nav, NavItem} from 'react-bootstrap';
import {Route} from 'react-router';
import {Switch} from 'react-router-dom';
import {LinkContainer} from 'react-router-bootstrap';
import Login from './Login';
import SignUp from './SignUp';
import PollResults from './PollResults';
import Dashboard from './Dashboard';
import SitePolls from './SitePolls';
import Poll from './Poll';
import {logOut} from '../actions/actionCreators';

const NoMatch = () => <h2>This page does not exist! Please go back!</h2>;

const Footer = () => (
  <div className="footer-style">
    <p>Copyright &copy; 2017 Chris Cantu. All Rights Reserved</p>
  </div>
);

class RoutedApp extends Component {
  constructor(props) {
    super(props);
    this.state = {
      header: <Navbar />,
      main: this.getMainContent(),
    };

    this.updateHeader = this.updateHeader.bind(this);
  }

  componentDidMount() {
    this.updateHeader();
  }

  getMainContent() {
    return (<main>
        <Switch>
          <Route exact path="/allPolls" component={SitePolls} />
          <Route path="/polls/:name/:id" component={Poll} />
          <Route path="/results/:name/:id" component={PollResults} />
          <Route exact path="/" render={props => <Login navBarRender={this.updateHeader} {...props} />} />
          <Route exact path="/signUp" render={props => <SignUp navBarRender={this.updateHeader} {...props} />} />
          <Route exact path="/dashboard" component={Dashboard} />
          <Route path="*" component={NoMatch} />
        </Switch>
    </main>);
  }

  getDynamicHeader(loggedIn) {
    if (loggedIn) {
      return (<Navbar id="navBar">
        <Navbar.Header>
          <Navbar.Brand className="navItem">Voterific</Navbar.Brand>
        </Navbar.Header>
        <Nav>
          <LinkContainer to="/Dashboard">
            <NavItem className="navItem">Home</NavItem>
          </LinkContainer>
          <LinkContainer to="/allPolls">
            <NavItem>All Polls</NavItem>
          </LinkContainer>
        </Nav>
        <Nav pullRight>
          <LinkContainer to="/">
            <NavItem onClick={() => { /*store.dispatch(logOut(this.updateHeader)); */}}>
              Log Out
            </NavItem>
          </LinkContainer>
        </Nav>
      </Navbar>);
    }

    return (<Navbar>
      <Navbar.Header>
        <Navbar.Brand className="navItem">Voterific</Navbar.Brand>
      </Navbar.Header>
      <Nav>
        <LinkContainer to="/allPolls">
          <NavItem>All Polls</NavItem>
        </LinkContainer>
      </Nav>
      <Nav pullRight>
        <LinkContainer to="/">
          <NavItem>Log In</NavItem>
        </LinkContainer>
        <LinkContainer to="/signUp">
          <NavItem>Sign Up</NavItem>
        </LinkContainer>
      </Nav>
    </Navbar>);
  }

  updateHeader() {
    this.setState({
       // refactor getState
     // header: this.getDynamicHeader(store.getState().currentUser.loggedIn || false),
    });
  }

  render() {
    return (<div>
      {this.state.header}
      {this.state.main}
    </div>);
  }
}

RoutedApp.propTypes = {
  header: React.PropTypes.any,
  main: React.PropTypes.func,
};

export default RoutedApp;



